.navbar1{
    display: flex;
    justify-content:space-around;
    box-shadow: 0px 1px 3px -2px black;
    align-items: center; 

}

.nav-logo1{
    display: flex;
    align-items: center;
    gap: 10px;
}

.nav-logo1 p{
    color:red;
    font-size: 25px;
    font-weight: 600;
}

.nav-login-cart1{
    display: flex;
    align-items: center;
    gap:20px;
}

.log-btn{
    width: 130px;
    height: 50px;
    outline: none;
    border: 1px solid #7a7a7a;
    border-radius: 75px;
    color:black;
    font-size: 20px;
    font-weight: 500;
    background-color: white;
    cursor: pointer;
    margin-left: 200px;
}

.log-btn:active{
    background:grey;
}

.create-btn{
    width: 180px;
    height: 50px;
    outline: none;
    border: 1px solid #7a7a7a;
    border-radius: 75px;
    color:black;
    font-size: 20px;
    font-weight: 500;
    background-color: white;
    cursor: pointer;
}

.create-btn:active{
    background:grey;
}


@media (max-width: 1280px){
    .navbar1{
       padding: 12px 40px;
    }
    .nav-logo1 img{
       width: 40px;
    }
   
    .nav-logo1 p{
       font-size: 25px;
    }

    .log-btn{
       width: 120px;
       height: 45px;
       font-size: 16px;
    }

    .create-btn{
        width: 180px;
        height: 45px;
        font-size: 16px;
    }

   }
   
   @media (max-width: 1024px){
   .navbar1{
       padding: 12px 30px;
   }
 
   .log-btn{
       width: 80px;
       height: 35px;
       font-size: 14px;
   }
   
   .create-btn{
    width: 160px;
       height: 35px;
       font-size: 14px;
   }
   }
   
   
   @media (max-width:800px){
       .navbar1{
        margin-left: 20px;
       }

       .log-btn{
        width: 80px;
        height: 35px;
        font-size: 14px;
        margin-left: -80px;
    }
    
    .create-btn{
     width: 160px;
        height: 35px;
        font-size: 14px;
    }
   
   }
   
   @media (max-width:500px){
    .navbar1{
        margin-left:-10px;
    }

    .log-btn{
        width: 60px;
        height: 30px;
        font-size: 15px;
        margin-left: 40px;

    }
    
    .create-btn{
     width: 130px;
        height: 30px;
        font-size: 15px;
        margin-left: 5px;
    }
   }

