.loginsignup{
    width: 100%;
    height: 100vh;
    background:#fce3fe;
    padding-top: 100px;
}

.loginsignup-container{
    width: 580px;
    height:500px;
    background: white;
    margin: auto;
    padding: 40px 60px;
    border-radius: 10px;
}

.loginsignup-container1{
    width: 580px;
    height:400px;
    background: white;
    margin: auto;
    padding: 40px 60px;
    border-radius: 10px;
}

.loginsignup-container h1{
    margin: 20px 0px;
}

.loginsignup-filelds{
    display: flex;
    flex-direction: column;
    gap: 29px;
    margin-top: 30px;
  
}

.loginsignup-filelds input{
    height: 50px;
    width: 100%;
    padding-left:20px;
    border: 1px solid#c9c9c9;
    outline: none;
    color: #5c5c5c;
    font-size: 18px;
    border-radius: 10px;
}

.loginsignup-container button{
    width: 580px;
    height: 50px;
    color: white;
    background: #ff4141;
    margin-top: 30px;
    border: none;
    font-size: 24px;
    font-weight: 500;
    cursor: pointer;
    margin-left:10px;
    border-radius: 10px;
}

.loginsignup-container1 button{
    width: 580px;
    height: 50px;
    color: white;
    background: #ff4141;
    margin-top: 30px;
    border: none;
    font-size: 24px;
    font-weight: 500;
    cursor: pointer;
    margin-left:10px;
    border-radius: 10px;
}

.loginsignup-login{
    margin-top: 20px;
    color: #5c5c5c;
    font-size: 18px;
    font-weight: 500;
    margin-left: 10px;
}

@media (max-width:1280px){
    .loginsignup{
        padding-top:50px ;
    }
    .loginsignup-container{
        width: auto;
        max-width: 500px;
        max-height: 470px;
    }

    .loginsignup-container1{
        width: auto;
        max-width: 500px;
        max-height: 470px;
    }

    .loginsignup-container h1{
        margin: 10px 0px;
    }

    .loginsignup-container1 h1{
        margin: 10px 0px;
    }

    .loginsignup-filelds{
        gap: 20px;
        margin-top: 20px;
    }

    .loginsignup-filelds input{
        height: 45px;
        width: 93%;
    }

    .loginsignup-container button{
        width: 90%;
        margin-left: 20px;
    }

    .loginsignup-container1 button{
        width: 90%;
        margin-left: 20px;
    }

    .loginsignup-login{
        margin-left:20px ;
    }
}