.cartitem{
    margin: 100px 120px;
}

.cartitem hr{
    height: 3px;
    background: #e2e2e2;
    border: 0;
}

.cartitem-format-main{
    display: grid;
    grid-template-columns: 1fr 2fr 1fr 1fr 1fr 1fr;
    align-items: center;
    gap:40px;
    padding: 20px 0px;
    color: #454545;
    font-size: 18px;
    font-weight: 600;
}

.carditem-format{
    font-size: 17px;
    font-weight: 500;
}

.carticon-product-icon{
    height: 62px;

}

.cartitem-remove-icon{
    width: 15px;
    margin: 0px 40px;
    cursor: pointer;
}

.cartitem-quantity{
    width: 64px;
    height: 50px;
    border: 2px solid #ebebeb;
    background: #fff;
    margin-left: 13px;
}

.old-p{
    margin-left: 13px;
}

.new-p{
    margin-left: 13px;
}

.carditem-down{
    display: flex;
    margin: 100px 0px;
}

.cartitem-total{
    flex: 1;
    display: flex;
    flex-direction: column;
    margin-right: 200px;
    gap: 40px;
}

.cartitem-total-item{
    display: flex;
    justify-content: space-between;
    padding: 15px 0px;
}

.car-btn{
    width: 260px;
    height: 58px;
    outline: none;
    border: none;
    background: #ff5a5a;
    color: white;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    border-radius: 10px;
}

.carditems-promocode{
    flex: 1;
    font-size: 16px;
    font-weight: 500;
}

.carditems-promocode p{
    color: #555;
    margin-left: 30px;
}

.carditem-promobox{
    width: 500px;
    margin-top: 15px;
    margin-left: 20px;
    height: 58px;
    background: #eaeaea;
    border-radius: 10px;
}

.carditem-promobox input{
    border: none;
    outline: none;
    background: transparent;
    font-size: 16px;
    width: 330px;
    height: 50px;
    border-radius: 10px;
}

.carditem-promobox button{
    width: 160px;
    height: 58px;
    font-size: 16px;
    background: black;
    color: white;
    border-radius: 10px;
    cursor: pointer;
}

.buyprodect{
    height: 50px;
    width: 100px;
    margin-left: -20px;
    border-radius: 10px;
    background: red;
    color: white;
    cursor: pointer;
}

@media(max-width:1280px){
    .cartitem{
        margin: 60px 50px;
    }

    .cartitem-format-main{
        grid-template-columns: 0.5fr 3fr 0.5fr 0.5fr 0.5fr 0.5fr;
        gap: 20px;
        padding: 15px 0px;
        font-size: 15px;
    }

    .carticon-product-icon{
        height: 50px;
    }

    .cartitem-remove-icon{
        margin: auto;
    }

    .cartitem-quantity{
        width: 40px;
        height: 30px;
    }

    .carditem-down{
        margin: 18px 0px;
        flex-direction: column;
        gap: 18px;
    }

    .cartitem-total{
        margin: 0;
    }

    .cartitem-total button{
        max-width: 200px;
        height: 45px;
        font-size: 13px;
    }

    .carditem-promobox{
        width: auto;
        max-width: 500px;
    }

    .carditem-promobox input{
        width: 100%;

    }

    .carditem-promobox button{
        width: 120px;
        margin-left: -125px;
    }
}

@media (max-width:500px){
    .cartitem-format-main{
        display: none;
        grid-template-columns: 0.5fr 3fr 0.5fr;
        gap: 10px;
    }

    .carditem-format{
        display: grid;
    }

    .cartitem-quantity{
        margin-left: 5px;
    }
}