.navbar{
    display: flex;
    justify-content:space-around;
    box-shadow: 0px 1px 3px -2px black;
    align-items: center; 

}

.nav-logo{
    display: flex;
    align-items: center;
    gap: 10px;
}

.nav-logo p{
    color:red;
    font-size: 25px;
    font-weight: 600;
}

.nav-menu{
    display: flex;
    list-style: none;
    align-items: center;
    gap: 30px;
    font-size: 20px;
    font-weight: 500;
    color:gray;
}

.nav-menu li{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3px;
    cursor: pointer;
}

.nav-menu hr{
    border: none;
    width: 80%;
    height: 3px;
    border-radius: 10px;
    background-color: red;
}

.nav-login-cart{
    display: flex;
    align-items: center;
    gap: 35px;
}

.nav-login-cart button{
    width: 130px;
    height: 50px;
    outline: none;
    border: 1px solid #7a7a7a;
    border-radius: 75px;
    color:black;
    font-size: 20px;
    font-weight: 500;
    background-color: white;
    cursor: pointer;
}

.nav-login-cart button:active{
    background:grey;
}

.nav-card-count{
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    margin-top: -35px;
    margin-left: -55px;
    font-size: 14px;
    background:red;
    color: white;
}

.nav-dropdown{
 display: none;
}

@media (max-width: 1280px){
 .navbar{
    padding: 12px 50px;
 }
 .nav-logo img{
    width: 40px;
 }

 .nav-logo p{
    font-size: 25px;
 }
 
 .nav-menu{
  gap: 30px;
  font-size: 16px;    
 }

 .nav-login-cart{
    gap: 30px;
 }
 .nav-login-cart button{
    width: 120px;
    height: 45px;
    font-size: 16px;
 }

 .nav-card-count{
    margin-left: -40px;
    font-size: 12px;
 }
}

@media (max-width: 1024px){
.navbar{
    padding: 12px 30px;
}
.nav-menu{
    gap: 25px;
    font-size: 14px;
}

.nav-login-cart button{
    width: 80px;
    height: 35px;
    font-size: 14px;
}

.nav-login-cart img{
    width: 30px;
}

.nav-card-count{
    width: 18px;
    height: 18px;
}
}


@media (max-width:800px){
    .navbar{
        padding: 10px 0px;
    }
    .nav-dropdown{
        display: block;
        width: 30px;
        height: 30px;
        align-items: center;
        rotate: -90deg;
        border: 2px solid black;
        border-radius: 50%;
    }

    .nav-menu{
        display:none;
        height: 80px;
        width: 100%;
        position: absolute;
        background:white ;
        justify-content: center;
        top: 50px;
    }

    .nav-menu-visible{
        display: flex;
    }

    .nav-dropdown.open{
transform: rotate(90deg);
    }
}

@media (max-width:500px){
 .navbar{
    width: 8px 0px;
    gap: 0;
 }
 .nav-logo{
    transform: scale(0.8);
 }
 .nav-menu{
    height: 70px;
    top: 50px;
 }
 .nav-login-cart{
    transform: scale(0.8);
 }
}