.hero{
    display: flex;
    height: 100vh;
    background-color:lightblue;
}

.hero-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap:10px;
    padding: 180px;
    line-height: 1.1;
}

.hero-left h2{
    color: black;
    font-size: 26px;
    font-weight:600;
}

 .hero-left p{
    color: #171717;
    font-size: 40px;
    font-weight: 700;
}

.hand-hand-icon{
    display: flex;
    align-items: center;
    gap: 10px;
}

.hand-hand-icon img{
    width: 130px;
}

.hero-latest-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    width: 250px;
    height: 150px;
    border-radius: 70px;
    margin-top: 30px;
    background-color: red;
    color: white;
    font-size: 22px;
    font-weight: 500;
}

.hero-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 250px;
}

@media(max-width:1280px){
    .hero-left{
        padding: 100px;
    }
    .hero-left h2{
        font-size: 22px;
    }
    .hero-left p{
        font-size: 60px;
    }

    .hand-hand-icon img{
        width: 80px;
    }
    .hero-latest-btn{
        gap: 10px;
        width: 250px;
        height: 60px;
        margin-top: 20px;
        font-size: 18px;
    }
    .hero-right img{
        width: 500px;
    }
}


@media(max-width:1024px){
    .hero-left{
        padding: 80px;
    }
    .hero-left h2{
        font-size: 20px;
    }
    .hero-left p{
        font-size: 50px;
    }

    .hand-hand-icon img{
        width: 65px;
    }
    .hero-latest-btn{
        width: 220px;
        height: 50px;
        font-size: 16px;
    }
    .hero-right img{
        width: 400px;
    }
}


@media(max-width:800px){
    .hero-left{
        padding: 30px;
    }
    .hero-left h2{
        font-size: 16px;
    }
    .hero-left p{
        font-size: 40px;
    }

    .hero.hand-hand-icon img{
        width: 50px;
    }
    .hero-latest-btn{
        width: 175px;
        height: 40px;
        font-size: 13px;
    }
    .hero-right img{
        width: 300px;
    }
}


@media(max-width:500px){
    .hero{
        flex-direction: column;
    }
    .hero-right{
        display: none;
    }

    .hero-left h2{
        font-size: 18px;
    }

    .hero-left p{
        font-size: 50px;
    }

    .hand-hand-icon img{
        width: 55px;
    }

    .hero-latest-btn{
        width: 200px;
        height: 45px;
        font-size: 14px;
    }
    

}